    
    .testimonials {
        padding: 5rem 0 0 0;

   }
   
   .container.testimonials__container  {
        margin: 0 auto;
   }

   .client__avatar {
       width: 30rem;
       aspect-ratio: 1/1;
       margin: 0 auto 1rem;
   }

   .testimonial {
       text-align: center;
       padding: 2rem;
       border-radius: 2rem;
       user-select: none;
   }

   .client__name {
       padding-top: 1rem;
       
   }

   .client__text {
    padding-bottom: 2rem;
   }

   .swiper-pagination-clickable .swiper-pagination-bullet {
       background: var(--color-primary);
   }


   
   .logotailwinds {
       padding-bottom: 0.5rem;
   }


   .wrapper_content p {
       font-size: 12px;
       padding-top: 0.7rem;
   }


   .logo__ {
       display: block;
       margin: 0 auto;
       width: 40px;
       height: auto;
   }


   /* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1524px) {
    .container.testimonials__container  {
        width: 80%;
 
    }
 
    .client__avatar {
        width: 30rem;
        height: auto;
        aspect-ratio: 1/1;
        margin: 0 auto 1rem;
    }


   .testimonials {
       padding: 1rem 0 0 0;

  }
}


/* =============MEDIA QUERIES (SMALL DEVICES)============== */

@media screen and (max-width: 600px) {
       
    .container.testimonials__container  {
        width: 100%;
 
    }
 
    .client__avatar {
        width: 20rem;
        height: auto;
        aspect-ratio: 1/1;
        margin: 0 auto 1rem;
    }
    

       .wrapper__logo {
           width: 80%;
           flex-direction: column;
           align-items: center;
       }

       .wrapper_content {
           padding: 2rem 0;
        }

        
}
