.portfolio2 {
    padding-top: 2rem;
}

.portfolio2__container {
display: grid;
grid-template-columns: repeat(3, 1fr);
gap: 2.5rem;
}

.portfolio2__item {

background: transparent;
padding: 1.3rem;
border-radius: 2rem;
border: 1.7px solid var(--color-primary-variant);
transition: var(--transition);
text-align: center;
}

.portfolio2__item:hover {
background: var(--color-primary-variant);
border: 1px solid transparent;
}

.portfolio2__item-image {

border-radius: 1.5rem;
overflow: hidden;
}

.portfolio2__item h3 {
margin: 1.2rem 0 0.5rem;
}

.item__text_h5 {
    padding-bottom: 2rem;
}

.portfolio2__item-cta {
display: flex;
gap: 1rem;
margin-bottom: 1rem;
justify-content: center;
}

.portfolio2 {
    margin-top: 7rem;
}

.item__text_h3 {
    font-size: 1.5rem;
}

.web__title {
    font-size: 1.5rem;
    padding-bottom: 2rem;
}


/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1024px) {

    .portfolio2__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }



}


/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 600px) {
    .portfolio2__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

}