
header {
    height: 100vh;
    overflow: hidden;
}

.header__container {
    height: 100%;
    position: relative;
}


.header__container h1 {
    font-weight: 500;
}



/* =========HEADER SOCIALS========== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 4rem;
    background: var(--color-primary);
}

.header__container {
    padding-top: 2rem;
}

/* =======Main text & image ==========*/

.maintext___container {
    display: flex;
    flex-direction: column;
    padding: 5rem 0 0 0;
    line-height: 1.2;
    
}


.mainpage-text1 {
font-family: 'Righteous', cursive;
font-size: 4.8rem;
}

.mainpage_text_h4 {
    font-size: 2.5rem;
    font-weight: 300 !important;
}

.mainpage-text5 {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-top: 1.5rem;
}

.mainpage-text5 h5{
    font-size: 1.2rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.6);
    padding-right: 2rem;
    padding-left: 0.6rem;
}

.mainpage_btn_text5{
    display: inline-block;
    padding: 0.7rem 2rem !important;
}

.mainpage-text2,
.main__text {
    font-family: 'Righteous', cursive;
    font-size: 8rem;
    background: linear-gradient(to right top, #F4976C 30%,#FF5F6D 70%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


.bttn {
  border: 1px solid currentColor;
  border-radius: 2rem;
  color: var(--color-primary);
  font-size: 1.2rem;
  font-weight: 300;
  overflow: hidden;
  padding: 1rem 2rem;
  position: relative;
  transition: .2s transform ease-in-out;
  will-change: transform;
  z-index: 0;
}
.bttn::after {
    background-color: var(--color-primary);
    border-radius: 2rem;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-100%, 0) rotate(10deg);
    transform-origin: top left;
    transition: .2s transform ease-out;
    will-change: transform;
    z-index: -1;
}
.bttn:hover::after{
    transform: translate(0, 0);
}
    
.bttn:hover {
    border: 1px solid transparent;
    color: rgb(255, 255, 255);
    transform: scale(1.01);
    will-change: transform;
}

.bttn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
    font-weight: 500;
}


/* =========ME========== */


/* .mainimage__container {
    display: flex;
    justify-items: right;
    
} */

.me {
    width: 45rem;
    height: 45rem;
    position: absolute;
    bottom: -16rem;
    right: -3rem;
    z-index: -1;
}



/* =========SCROLL DOWN========== */

.scroll__down {
    position: absolute;
    right: -5.5rem;
    bottom: 10.5rem;
    transform: rotate(90deg);
    font-weight: 400;
    font-size: 0.9rem;
    padding-bottom: 3rem;
}


/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (min-width: 2300px) {

   
    .me {
        width: 55rem;
        height: 55rem;
        bottom: -20rem;
        right: -3rem;
    }

    .maintext___container {

        padding: 12rem 0 0 0;

    }

    .mainpage-text1 {

        font-size: 6rem;
        }
        
    .mainpage-text2,
    .main__text {

        font-size: 10rem;
    
    }
    


}



@media screen and (max-width: 1024px) {

    .cta {
        padding-top: 3.5rem;
    }

        header {
            padding-top: 2rem;
            height: 85vh;
        }

        .me {
            width: 70%;
            bottom: -30rem;
            right: -8rem;
        }

      

        .header__socials {
            bottom: -3rem;
        }

        
        .maintext___container {
            padding: 3rem 0 0 0;
            line-height: 1.5;
        }

        .maintext___container > div {
            text-align: center;
        }
    
        .mainpage-text1 {
            font-size: 2rem;
         }
            
        .mainpage-text2,
        .main__text {
            font-size: 6rem;
        }

        .mainpage-text5 {
            flex-direction: column;
            justify-content: center;
            gap: 2rem;
         }
        
  
}



/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 600px) {
    

    .scroll__down {
        display: none;
    }

    header {
        padding-top: 2rem;
        height: 100vh;
    }

    .cta {
        padding-top: 2rem;
    }

    
        .me {
            display: none;
        }

        .header__socials {
            
            top: 4.7rem;
            padding-left: 1rem;
        }

        
        
        .header__socials::after {
            display: none;
        }
        
        
        .maintext___container {
            padding: 2rem 1rem 0 1rem;
            line-height: 1.5;
        }

        .maintext___container > div {
            text-align: center;
        }
    
        .mainpage-text1 h4{
            font-size: 1.7rem;
         }
            
        .mainpage-text2,
        .main__text {
            font-size: 3.7rem;
        }

        .mainpage-text5 > h5{
            flex-direction: column;
            justify-content: center;
           
         }

         .mainpage-text5 h5{
            font-size: 1rem;
            font-weight: 300;
            color: rgba(255, 255, 255, 0.6);
            padding-bottom: 1rem;
            padding-right: 0;
            padding-left: 0;
        }

    } 