.portfolio {
        padding: 5rem 0 0 0;
}

.portfolio__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
}

.portfolio__item {
    background: transparent;
    padding: 1.3rem;
    border-radius: 2rem;
    transition: var(--transition);
}
.portfolio__item:hover {
    background: var(--color-bg-variant);
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 0 0;
}

.portfolio__item h2  {
    font-weight: 300;
}
.portfolio__h2 {
    font-size: 1.5rem;
}

.portfolio__item-cta {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.port__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.port__text {
    display: flex;
    flex-direction: column;

    
}

.port__button {
    padding-top: 1rem;
}
/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1024px) {

        .portfolio__container {
            grid-template-columns: 1fr 1fr;
            gap: 1.2rem;
        }

        .portfolio {
            padding: 3rem 0;
    }
    
        


    }
    
    
    /* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */
    
    @media screen and (max-width: 600px) {
        .portfolio__container {
            grid-template-columns: 1fr;
            gap: 1rem;
        }

    }