.nav2 {
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

.nav2 a {
    color: var(--color-white);
    position: relative;
}
.nav2 a::after {
    content: "";
    position: absolute;
    background-color: var(--color-primary);
    height: 1px;
    width: 0;
    left: 0;
    bottom: -10px;
    transition: 0.3s;
}

.nav2 a:hover {
    color: var(--color-primary);
}

.nav2 a:hover:after {
    width: 100%;
}

.nav2__logo {
    font-size: 1.5rem;
    font-weight: 500;
    display: inline-block;
}

.nav2__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 1.5rem;
    max-width: 90%;
    margin-right: auto;
    margin-left: auto;
}

.nav2__li {
    display: flex;
    width: 30rem;
    justify-content: space-evenly;
}


.jinny__logo {
    width: 2rem;
    margin-left: 5rem;
    color: #F4976C;
}


/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1024px) {

    .jinny__logo {
        width: 2rem;
        color: #F4976C;
        margin-left: 1rem;
    }

}

/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 600px) {

    .logo__wrapper {
        display: block;
        margin: 0px auto;
        padding-top: 1.5rem;
    }

    .nav2__li {
        display: none;
     }

     .jinny__logo {
        width: 2rem;
        color: #F4976C;
    }
}