.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .mymodal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 50rem;
    max-height: 60rem;
    margin: 0 auto;
    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 20px;
    
  }

  .modal___container {
    display: flex;
    justify-content: flex-start;
    padding: 5rem;
    color: black;
}

  
  .myoverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 1000;
  }

  .modal__portfolioImage {
    width: 30rem;
  }

  .modal__portfolioImage2 {
    width: 100%;
    padding: 3rem 3rem 5rem 3rem;
  }
  
  .modal__portfolioText {
    width: 30rem;
    padding-left: 5rem;
  }

  .modal__portfolioText h2 {
    font-size: 2.5rem;
    font-weight: 800;
  }

  .modal__portfolioText h5 {
    font-size: 1.2rem;
  }
 
  .modal__portfolioText p {
    font-size: 1.2rem;
    padding-top: 5rem;
  }

  .close___button {
      display: flex;
      justify-content: flex-end;
  }

  .modal__icon {
      width: 2rem;
      height: 2rem;
      cursor: pointer;
  }

  .ReactModal__Overlay {
    opacity: 0;
    transition: opacity 500ms ease-in-out;
  }
  
  .ReactModal__Overlay--after-open {
    opacity: 1;
  }
  
  .ReactModal__Overlay--before-close {
    opacity: 0;
  }
  

/* =============MEDIA QUERIES (MEDIUM DEVICES)============== */


  @media screen and (max-width: 1024px) {
  
    .mymodal {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 35rem;
      max-width: 45rem;
      max-height: 50rem;
      padding: 10px;
      width: 100%;
    }
  
    .modal___container {
      padding: 2rem;
  }

}



/* =============MEDIA QUERIES (SMALL DEVICES)============== */

@media screen and (max-width: 600px) {

  .mymodal {

    min-width: 20rem;
    max-width: 20rem;
    max-height: 30rem;
    padding: 10px;
    width: 100%;
  }

  .modal___container {
    display: flex;
    flex-direction: column;
    padding: 0;
    color: black;
}

.modal__portfolioImage {
  width: 100%;
  padding: 1rem;
}

.modal__portfolioImage2 {
  width: 100%;
  padding: 1rem;
}

.modal__portfolioText {
  width: 100%;
  padding-left: 0;
  text-align: center;
  padding: 1.5rem 1.5rem 2rem 1.5rem;
}

.modal__portfolioText h2 {
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.2;
}

.modal__portfolioText h5 {
  font-size: 1.5rem;
  padding-top: 1rem;
}

.modal__portfolioText p {
  font-size: 1rem;
  padding-top: 4rem;
}
}
