section {
    margin-top: 3rem !important;
}

.experience {
    padding: 0.2rem 0 0 0;
}

.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience_work_wrapper {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    padding: 5rem 0 0 0;
}


.experience_work_wrapper> article {
    text-align: center;
    border-color: var(--color-bg-variant) !important;
    border: 1px solid;;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
}

.article > h3 {
    color: var(--color-primary);
    font-size: 1.2rem;
}
.article > h5 {
    color:  #6969a1;
}

.article > p {
    padding-top: 1.5rem;
    color: rgb(239, 239, 239);
}

.article > p > span {
    color: var(--color-primary);
    padding-right: 0.5rem;
}

.work_h2 {
    color: var(--color-primary);
    text-align: center;
    padding-top: 5rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
    column-gap: 2rem;
    justify-items:center;
}

.experience__details {
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}




/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1024px) {

    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience {
        padding: 3rem 0 0 0;
    }

    .experience_work_wrapper {
        grid-template-columns: 1fr;
    }

    .experience_work_wrapper > .article {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
}



/* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 600px) {

    .experience__container {
        gap: 1rem;

    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;

    }
}