
    .testimonials {
         padding: 5rem 0 0 0;

    }

    .logotailwinds {
        padding-bottom: 0.5rem;
    }


    .wrapper_content p {
        font-size: 12px;
        padding-top: 0.7rem;
    }

 
    .logo__ {
        display: block;
        margin: 0 auto;
        height: auto;
    }


    .wrapper__logo {
        width: 60rem;
        padding-top: 3rem;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        margin: 0 auto;
        bottom: 0;
        padding-bottom: 3rem;
    }
 
    .wrapper_content {
       text-align: center;
    }

    /* ==============MEDIA QUERIES (MEDIUM DEVICES) ============== */

@media screen and (max-width: 1024px) {
  

    .wrapper__logo {
        width: 40rem;

    }

 

}



/* =============MEDIA QUERIES (SMALL DEVICES)============== */

@media screen and (max-width: 600px) {
        .container.testimonials__container {
            width: var(--container-width-sm);
        }

        .client__review {
            width: var(--container-width-sm);
        }

        .wrapper__logo {
            width: 80%;
            flex-direction: column;
            align-items: center;
        }

        .wrapper_content {
            padding: 2rem 0;
         }

         
}
