footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    display: inline-block;
}

.footer_h2 {
    font-weight: 800;
    font-size: 1.8rem;
}

.footer_h2, .footer_h4 {
    color: var(--color-bg);
}

.footer_h2:hover, .footer_h4:hover {
    color: var(--color-bg-variant);
    cursor: pointer;
}

.footer_h4 {
    margin-bottom: 2rem;
}

.logo_black {
    width: 2rem;
    height: auto;
}

.footer_container {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer_text_container {
    display: flex;
    flex-direction: column;
    text-align: left;
}



.footer_img_container {
    padding-left: 9rem;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;
}

/* .footer___socials {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
} */


.footer__socials a {
    display: flex;
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
    padding: 0.8rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: var(--color-bg);
    color: var(--color-white);
    border: 1px solid transparent;
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

.copyright_sapn {
    font-weight: 700;
    padding-right: 0.1rem;
}

@media screen and (max-width: 1040px) {
    .footer_container {
           
        margin: 0 auto;
        flex-direction: row;
        align-items: center;
    }
    
    .footer_text_container {
        display: none;
    }
    
    .footer_img_container {
        padding-left: 0;
        text-align: left;
    }

    .footer__socials {
        display: flex;
        justify-content: center;
        gap: 0;
        margin-bottom: 2rem;
        
    }

    .footer__copyright {
        padding-right: 1rem;
    }
    
}
    
    
    /* =============MEDIA QUERIES (SMALL DEVICES)============== */
    
    @media screen and (max-width: 600px) {
        .permalinks {
            flex-direction: column;
            gap: 1.5rem;
        }

        .footer__socials {
            margin-bottom: 2.6rem;
            border-radius: 0;
        }

        
    }
    